* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'montseratt' , sans-serif;
}

.app {
  background-image: url("./assets/cold.png");
  background-repeat: no-repeat; 
  /* position: absolute; */
  display: flex;
  justify-content: center;
  align-items: center;
  background-position: center;
  transition: 0.4s ease-out;
}

.app.warm {
  background-image: url("./assets/warm.png");
}

main {
  min-height: 100vh;
}

main {
  min-height: 100vh;
  background: linear-gradient(to-bottom, rgba(0,0,0,0.2), rgba(0,0,0,0.75));
  padding: 25px;
}

.search-box {
  width: 100%;
  margin: 0 0 75px;
}

.search-box .search-bar{
  position: relative;
  display: block;
  width: 85%;
  padding: 5px;
  margin:auto;
  margin-top: 120px;
  appearance: none;
  background: none;
  border: none;
  outline: none;
  background-color: rgba(255,255,255,0.5);
  border-radius: 10px;

  box-shadow: 0px 5px rgba(0,0,0,0.2);
  color: #313131;
  font-size: 20px;

  transition: 0.4s ease;

}

.search-box .search-bar:focus {
  background-color: rgba(255,255,255,0.75);
}

.location-box .location {
  color: #000;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(255,255,255,0.5);
}

.location-box .date {
  color: #fff;
  font-size: 20px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(50,50,50,0.5);
}

.weather-box {
  text-align: center;
}

.weather-box .temp {
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(255,255,255,0.2);
  border-radius: 16px;  

  padding: 15px 25px;
  color: #fff;
  font-size: 60px;
  font-weight: 900;
  text-shadow: 3px 6px rgba(50,50,70,0.5);
  text-align: center;
  box-shadow: 3px 6px rgba(0,0,0,0.2);
}

.weather-box .weather {
  color: #fff;
  font-size: 28px;
  font-weight: 700;
  text-shadow: 3px 3px rgba(50,50,70,0.5);

}